const state={
  name:''
};
const getters={
  getName(state){ 
    return state.name;
  }
};
const mutations={
  setName(state,val){ 
    state.name = val;
  }
};
const actions={
  setName(context,val){ 
    context.commit('setName',val);
  }
};
export default {
  namespaced:true,//
  state,
  getters,
  mutations,
  actions
};