const commonObj = {
	/*
	*	JSON
	*	obj JSON
	*	let newObj = deepCopy(oldObj)
	*	vue
	*/
	deepCopy: function (obj) {
		if(typeof obj == "object" && Object.prototype.toString.call(obj).toLowerCase() == "[object object]" && !obj.length){
            let res = {};
            for (var key in obj) {
              if (obj[key] === undefined || obj[key] === null) {
                /**/
              } else {
                res[key] = commonObj.deepCopy(obj[key]);
              }
            }
            return res;
        } else {
            return obj;
        }
	},
	/*
	*	
	*	num numberstring
	*         symbol ,
	*	let numberStr = thousandNum(5684953.25)    5,684,953.25
	*         let numberStr = thousandNum(5684953.25,'#')    5#684#953.25
	*	vue
	*/
	thousandNum: function (num, symbol) {
		if (symbol == undefined) symbol = ',';
		if (typeof (num) == 'number') {
			num = num.toString();
		}
		if (typeof (num) == 'string') {
			var pos_decimal = num.indexOf('.');
			var decimalStr = '', intStr = '';
			if (pos_decimal < 0) {
				intStr = num;
				decimalStr = '';
			} else {
				intStr = num.substring(0, pos_decimal);
				try {
					decimalStr = num.substring(pos_decimal+1);
				} catch (e) {
					decimalStr = '';
				}
			}
			var preIntStr = intStr;
			if (preIntStr != '') {
				intStr = '';
				var c = 0;
				for (var i = preIntStr.length-1; i >= 0; i--) {
					if ((c + 1) % 3 == 0) {
						if (i == 0) {
							intStr = preIntStr[i] + intStr;
						} else {
							intStr = symbol + preIntStr[i] + intStr;
						}
					} else {
						intStr = preIntStr[i] + intStr;
					}
					c++;
				}
			}
			if (decimalStr == '') {
				return intStr;
			} else {
				return intStr + '.' + decimalStr;
			}
		} else {
			return num;
		}
	},
	/*
	*	JSON
	*	jQueryextend
	*	vue
	*/
	extend: function () {
	  let deep = arguments[0], res = {}, index = 0;
	  if (deep === undefined) {
		/*false*/
		return false;
	  }
	  if (typeof deep != 'boolean') {
		/*1boolean12*/
		res = deep;
		deep = false;
		index = 1;
	  } else {
		/*1boolean*/
		res = arguments[1];
		if (res === undefined) {
		  /*21*/
		  return deep;
		} else {
		  /*223*/
		  index = 2;
		}
	  }

	  function checkJSONObj(obj) {
		if (typeof obj == "object" && Object.prototype.toString.call(obj).toLowerCase() == "[object object]" && !obj.length){
		  return true;
		} else {
		  return false;
		}
	  }

	  /*JSON*/
	  if (!checkJSONObj(res)) return res;

	  /**/
	  while (arguments[index] !== undefined){
		/*JSON,*/
		if (typeof arguments[index] == "object" && Object.prototype.toString.call(arguments[index]).toLowerCase() == "[object object]" && !arguments[index].length){
		  for (let key in arguments[index]) {
			if (arguments[index][key] === undefined || arguments[index][key] === null) {
			  /**/
			} else if (checkJSONObj(arguments[index][key])){
			  /*JSON*/
			  if (!deep) {
				/**/
				res[key] = arguments[index][key];
			  } else {
				/**/
				let source = res[key];
				if (!checkJSONObj(res[key])) {
				  source = {}
				}
				res[key] = commonObj.extend(deep,source,arguments[index][key]);
			  }
			} else {
			  /*JSON*/
			  res[key] = arguments[index][key];
			}
		  }
		}
		index++;
	  }
	  return res;
	},
	/**/
	regularCheck:{
		/**/
		'phone':function(val){
			return /^1(3|4|5|6|7|8|9)\d{9}$/.test(val);
		},
		/*
		*	
		*	str numberstring
		*         n 2
		*	let flag = positiveDecimal(5684953.254)    false
		*         let flag = positiveDecimal(5684953.2544)    true
		*	vue
		*/
		positiveDecimal: function (str, n) {
			if (typeof str == 'number') str = str.toString();
			if (n == undefined) n = 2;
			var regStr = '';
			if (n < 1) {
				regStr = '^\\d+$';
			} else {
				regStr = '^(([1-9]{1}\\d*)|(0{1}))(\\.\\d{1,' + n + '})?$';
			}
			var reg = new RegExp(regStr, 'g');
			var res = reg.test(str);
			return res;
		}
	},
	/*
	*	
	*	val 
	*         arr 
	*         name 
	*         mul 
	*         valName 
	*	let n = getArrIndex('2',['1','2'])    1
	*         let n = getArrIndex('2',[{value:'1'},{value:'2'}],'value')    1
	*         let n = getArrIndex([{id:'2'},{id:'3'}],[{value:'1'},{value:'2'},{value:'3'}],'value',true,'id')    [1,2]
	*	vue
	*/
	getArrIndex(val,arr,name=null,mul=false,valName=null){
		if (!mul) {
			return arr.findIndex((currentValue)=>{
				if (name===null){
					return currentValue === val;
				} else {
					return currentValue[name] === val;
				}
			});
		} else {
			let resArr = [];
			val.forEach((currentValueA,indexA)=>{
				let objA = currentValueA;
				if (valName!==null) {
					objA = currentValueA[valName];
				}
				let f = false;
				for (let i=0;i<arr.length;i++){
					let objB = arr[i];
					if (name!==null){
						objB = arr[i][name];
					}
					if (objB === objA) {
						resArr.push(i);
						f = true;
						break;
					}
				}
				if (!f) {
					resArr.push(-1);
				}
			});
			return resArr;
		}
	}
};

export default commonObj;